import { View } from '@tarojs/components';
import React, { useEffect } from 'react';
import { useShareAppMessage } from '@tarojs/taro';
import LectureList from '@/components/lecture-list/lecture-list';
import { getStorage } from '@/utils/tools';

import { ApiStore } from '@/store';
import PlayerFloat from '@/components/player-float/player-float';

const MineCourse = () => {
  useEffect(() => {}, []);

  useShareAppMessage((e: any) => {
    const cardShareInfo = {
      title: '我送你一张梨花听书7天VIP卡，快点击领取吧～',
      path: `/pages/activity/activity?from=friend&user_type=presented&channel_id=${getStorage('setChannelId') ?? ''}&sender_id=${
        getStorage('account').id ?? ''
      }`,
      imageUrl: 'https://static-1253442168.file.myqcloud.com/xupload/20210923_share-card_eE30n8c.png',
    };
    return e.from === 'menu' ? ApiStore.common.defaultShareInfo : cardShareInfo;
  });

  return (
    <View>
      <LectureList></LectureList>
      <PlayerFloat></PlayerFloat>
    </View>
  );
};

export default MineCourse;
